import React, {Component} from 'react';
import {BrowserRouter as Router, Route} from 'react-router-dom'
import Todos from './components/Todos';
import Header from "./components/layout/Header";
import AddTodos from "./components/AddTodos";
import About from "./components/pages/About"
import axios from "axios";
import './App.css';
var lastID = 3;
class App extends Component {
	state = { //init todos list
		todos: [
			{
				"userId": 1,
				"id": 1,
				"title": "delectus aut autem",
				"completed": false
			},
			{
				"userId": 1,
				"id": 2,
				"title": "quis ut nam facilis et officia qui",
				"completed": false
			},
			{
				"userId": 1,
				"id": 3,
				"title": "fugiat veniam minus",
				"completed": false
			},
		]
	};

	//get data from server using axios
	componentDidMount() {
		axios.get('https://jsonplaceholder.typicode.com/todos')
			.then(res => this.setState({todos:res.data})) //JS promise
	}

	// Toggle complete
	markComplete = (id) => {
		this.setState({
			todos: this.state.todos.map(todo => {
				if (todo.id === id) todo.completed = !todo.completed
				return todo;
			})
		})
		console.log('markcpl')
		console.log(this.state)
	};

	// Delete item
	markDelete = (id_clicked) => {
		this.setState(
			{
				todos: this.state.todos.filter(todo => todo.id !== id_clicked )
			}
		);

		/*
		this.state.todos.splice( //cool way to delete item using index
			this.state.todos.findIndex(({id}) => { //find item by id, return index
				return (id === id_clicked)
			}),1);
		console.log(this.state.todos);
		//Propagate changes
		this.setState({
			todos: this.state.todos
		})*/

		console.log('markdel')
		console.log(this.state);
	};

	// Add Todo
	addTodo = (title) => {
		console.log(title)
		this.setState({
			todos:
				this.state.todos.concat({
					id:++lastID,
					title:title,
					completed:false
				})
		})
	};
	render() {
		return (
			<Router>
				<div className="App">
					<Header/>
					<Route exact path = {home} render = {props => (
						<React.Fragment>
							<AddTodos addTodo={this.addTodo}></AddTodos>
							<Todos todos={this.state.todos}
										 markComplete={this.markComplete}
										 markDelete={this.markDelete}
							/>
						</React.Fragment>
					)}/>
					<Route path = {home+'/about'} component = {About}/>
				</div>
			</Router>
		);
	}
}

export default App;
export var home='/react_app'
